import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Produto } from '../../../modelo/Produto'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'produto-dados-details',   templateUrl: 'produto_dadosDetails.component.html' }) export class ProdutoDadosDetailsComponent implements OnInit {    produto: Produto;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.produto.Produto";                 let id = +params['id'];         this.produto = new Produto();         this.produto.cd_produto = id;         o.objetoJson = this.produto;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a produto. '; }      this.produto = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


