import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'produto-dados',   templateUrl: 'produto_dados.component.html' }) export class ProdutoDadosComponent implements OnInit {       displayMode: ProdutoDadosDisplayModeEnum;     displayModeEnum = ProdutoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = ProdutoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = ProdutoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = ProdutoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum ProdutoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


